import React, {useEffect, useMemo, useState} from 'react';
import {Box, Button, Flex, Text, useTable} from "components/uikit";
import {useWeb3React} from "@web3-react/core";
import useEvent from 'hooks/useEvent';
import {ethers} from "ethers";
import {parseEther} from "ethers/lib/utils";
import Table from "./Table";
import useAvatar from "../../contexts/useAvatar";
import contracts from "../../config/constants/contracts";
import useToast from "../../hooks/useToast";

const columns = [
  {
    name: 'id',
    hidden: true,
  },
  {
    name: 'trader',
    label: 'Trader',
    render: ({ value }: { value: React.ReactNode }): React.ReactNode => value,
  },
  {
    name: 'avatar',
    label: 'Avatar',
  },
  {
    name: 'token',
    label: 'Token',
  },
  {
    name: 'allocation',
    label: 'Allocation',
  },
  {
    name: 'symbol',
    label: 'symbol',
    hidden: true
  },
  {
    name: 'action',
    label: 'Action',
    render:({value})=> value
  },
]
function AvatarList(props) {
  const {history}=props
  const {account, active, library, connector} = useWeb3React()
  const _column = useMemo(()=> columns, [])
  const [addList, setAddList] = useState([])
  const {userAvatar} = useAvatar()
  const {toastSuccess, toastError} = useToast()
  const hasAvatarUsers = useMemo(()=> contracts.user.users.map(it=> it.toLowerCase()), [])
  const events = useEvent()

  const [dataSource, setDataSource] = useState([])

  const onAdd = ()=>{
    setAddList(prevState => {
      return [
        ...prevState,
        {
          id: dataSource.length+1,
          trader: 'N/A',
          avatar: userAvatar,
          token: 'N/A',
          allocation: 'N/A',
          action: 'manger',
          symbol: ''
        }
      ]
    })
  }

  const onCreate = ()=>{
    history.push('/deposit')
  }
  const onMint = async ()=>{
    if (active) {
      const erc20 = new ethers.Contract('0x47735caEfa2CCbC2f1C6a37337983dd0442F9755', contracts.erc20.abi, library.getSigner(account))
      try {
        const mint = await erc20.mint(parseEther('10'));
        await mint.wait();
        toastSuccess('Success')
      }catch (e) {
        toastError('Error', <Flex>
          <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
        </Flex>)
      }
    }
  }
  const onAddToMetamask= ()=>{
    connector.getProvider().then(async provider =>{
      await provider.request({
        method: 'wallet_watchAsset',
        params: {
          type: 'ERC20', // Initially only supports ERC20, but eventually more!
          options: {
            address: '0x47735caEfa2CCbC2f1C6a37337983dd0442F9755', // The address that the token is at.
            symbol: 'ST1', // A ticker symbol or shorthand, up to 5 chars.
            decimals: 18, // The number of decimals in the token
          },
        },
      });
    })
  }
  useEffect(()=>{
    setDataSource([
      ...events.map(it=> ({
        id:it.blockNumber,
        trader: it.trader,
        avatar: it.avatar,
        token: it.token,
        allocation: it.amount,
        action: 'manger',
        symbol: it.symbol
      })),
      ...addList
    ])
  }, [addList, events])
  return (
    <Box margin={20} padding={20} >
      <Text fontSize="30px">Trader List</Text>
      <Table key={JSON.stringify(events)} history={history}  _columns={_column} _data={dataSource}/>
      <Button disabled={!userAvatar} onClick={()=>{
        onAdd()
      }} margin={10} variant="primary"> Add </Button>
      <Button disabled={Number(userAvatar)!==0} onClick={()=>{
        onCreate()
      }} margin={10} variant="primary"> Create Avatar </Button>
      <Button disabled={Number(userAvatar)<=0} onClick={()=>{
        onCreate()
      }} margin={10} variant="primary"> Manage Avatar </Button>
      <Button onClick={()=>{
        onMint()
      }} margin={10} variant="primary"> Faucet </Button>
      <Button onClick={()=>{
        onAddToMetamask()
      }} margin={10} variant="primary"> Add ST1 Token to Metamask </Button>
    </Box>
  );
}

export default AvatarList;
