import {Box, Button, Text, useTable} from "components/uikit";
import StyledTh from "components/uikit/components/Table/example/header";
import React, {useCallback, useEffect, useState} from "react";
import {useWeb3React} from "@web3-react/core";
import {getAddress, isAddress} from "ethers/lib/utils";
import useAvatar from "../../contexts/useAvatar";

const Table = ({_columns, _data, history}) => {
  const {account} = useWeb3React()
  const {headers, rows} = useTable(_columns, _data, {
    sortable: true,
  })
  const {changeAvatar, userAvatar} = useAvatar()

  const onTrade = (row) => {
    changeAvatar(row.original.avatar)
    history.push(`/trading?trader=${row.original.trader}&token=${row.original.token}&avatar=${row.original.avatar}&amount=${row.original.allocation}&symbol=${row.original.symbol}`)
  }

  const onManager = (row) => {

    changeAvatar(row.original.avatar)
    history.push(`/trade?trader=${isAddress(row.original.trader)? row.original.trader : ''}&token=${row.original.token}&amount=${row.original.allocation}&avatar=${row.original.avatar}`)
  }
  const isManager = useCallback((avatar) => {
    console.log('avatar: >>>>>', avatar, userAvatar)
    // eslint-disable-next-line no-restricted-globals
    const _trader = isNaN(avatar)
    return userAvatar && userAvatar === (_trader ? '0x0' : getAddress(avatar))
  }, [userAvatar])
  const isTrader = useCallback((trader) => {
    // eslint-disable-next-line no-restricted-globals
    const _trader = isNaN(trader)
    console.log('_trader: >>>>>', _trader, account)
    return account && getAddress(account) === (_trader ? '0x0' : getAddress(trader))
  }, [account])
  return (
    <table>
      <thead>
      <tr>
        {headers.map((header) => (
          <StyledTh key={header.label} data-testid={`column-${header.name}`}>
            <Box padding={10} minWidth={240}><Text textAlign="left" fontSize="20px">{header.label}</Text></Box>
            {header.sorted && header.sorted.on ? <span data-testid={`sorted-${header.name}`}/> : null}
          </StyledTh>
        ))}
      </tr>
      </thead>
      <tbody>
      {rows.map((row, index) => (
        <tr data-testid={`row-${index}`} key={row.id}>
          {row.cells.map((cell, sIndex) => (
            // eslint-disable-next-line react/no-array-index-key
            <td key={sIndex} style={{padding: 10, borderBottom: '1px solid #ddd'}}>
              {/* eslint-disable-next-line no-nested-ternary */}
              {cell.value !== 'manger' ? <Box padding={10} maxWidth={300}>
                  <Text textAlign="left" style={{wordBreak: 'break-word'}} fontSize="20px">{cell.render()}</Text>
                </Box> :
                (<>
                  <Button isLoading={!isTrader(row.original.trader)}
                          variant="text" onClick={() => {
                    onTrade(row)
                  }}>Trade</Button>

                  <Button
                    isLoading={!isManager(row.original.avatar)}
                    variant="text" onClick={() => {
                    onManager(row)
                  }}>Manage</Button>
                </>)
              }
            </td>
          ))}
        </tr>
      ))}
      </tbody>
    </table>
  )
}
export default Table
