import React, {useCallback, useEffect, useMemo, useState} from 'react';
import styled from "styled-components";
import qs from 'query-string';
import {Contract} from "@ethersproject/contracts";
import {useWeb3React} from "@web3-react/core";
import {ethers} from "ethers";
import {formatEther, getAddress} from "ethers/lib/utils";
import {Box, Button, Flex, Input, Text} from "../../components/uikit";
import Select from "../../components/Select/Select";
import contracts from "../../config/constants/contracts";
import useToast from "../../hooks/useToast";
import {SupportedChainId} from "../../config/constants/chains";
import useAvatar from "../../contexts/useAvatar";
import useEvent from "../../hooks/useEvent";

const Row = styled(Box)`
  &:hover {
    background-color: #ddd;
    cursor: pointer;
  }
`

function amountFormat(amount) {
  return Math.floor((+amount || 0) * 10000) / 10000;
}
function SetTrader(props) {
  const {location} = props;
  const {chainId, active, account, library} = useWeb3React()
  const [search] = useState(qs.parse(location.search))
  const tokens = useMemo(() => [
    {
      label: 'ST1',
      value: '0x47735caEfa2CCbC2f1C6a37337983dd0442F9755'
    },
    {
      label: 'ST2',
      value: '0xd42D24648b4f4894a8b546fef96c37EBae8cAadB'
    },
  ], [])
  const [trader, setTrader] = useState(search.trader === 'N/A' ? '': search.trader)
  const [allowanceAmount, setAllowanceAmount] = useState('')
  const [token, setToken] = useState(tokens[0].value)
  const {toastSuccess, toastError} = useToast()
  const [setup, setSetup] = useState(search.trader && search.trader !== 'N/A' ? 2: 1)
  const [actionLock, setActionLock] = useState(false)
  const {avatarContract, currentAvatar, refresh, setRefresh} = useAvatar()
  const [avaible,setAvaible] = useState('')
  const events = useEvent();
  // const []
  const onApprove = async () => {
    if(active) {
      if(chainId === SupportedChainId.GOERLI) {

        try {
          if(search.trader) {
            const allowance = await avatarContract.allowance(search.trader, contracts.plugin.network[chainId]);
            if (allowance) {
              setSetup(2)
              return;
            }
          }
          setActionLock(true)
          console.log('avatarContract: >>>>>', avatarContract.address)
          const tx = await avatarContract.approve(trader, contracts.plugin.network[chainId], true)
          if (tx) {
            const res = await tx.wait();
            if (res) {
              toastSuccess('Success');
              setSetup(2)
              setActionLock(false)
            }
          }
        } catch (e) {
          toastError('Error', <Flex>
            <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
          </Flex>)
          setActionLock(false)
        }
      }else {
        toastError('Error', <Flex>
          <Text style={{wordBreak: 'break-word'}}>Unsupported network, please switch to goerli</Text>
        </Flex>)
        setActionLock(false)
      }
    }
  }
  const onTransfer = async ()=>{
    const iface = new ethers.utils.Interface(contracts.user.avatarAbi);
    const dataApprove = iface.encodeFunctionData("approveTokenSpent", [trader, token, ethers.utils.parseEther(allowanceAmount).toString()])
    // try to call
    try {
      setActionLock(true)
      const tx= await avatarContract.delegateCall(contracts.plugin.network[chainId], dataApprove)
      if (tx) {
        const res = await tx.wait();
        if (res) {
          toastSuccess('Success');
          setActionLock(false)
          setRefresh(prevState => !prevState)
        }
      }
    } catch (e) {
      toastError('Error', <Flex>
        <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
      </Flex>)
      setActionLock(false)
    }

  }



  useEffect(()=>{
    (async ()=> {
      try {
        if(active && currentAvatar.avatar) {
          const erc20 = new ethers.Contract(tokens[0].value, contracts.erc20.abi, library.getSigner(account))
          const balance = await  erc20.balanceOf(currentAvatar.avatar)
          const inter = new ethers.utils.Interface(contracts.user.avatarAbi);
          const _balance = formatEther(balance)
          // setAvaible(String(amountFormat(formatEther(balance))))
          if (events) {
            const res = events.filter(it=> getAddress(it.avatar) === getAddress(currentAvatar.avatar));
            // console.log('events: >>>>>', events)
            if (res.length>1) {
              const amount = res.reduce((prev,next)=> +prev.amount + +next.amount );
              console.log('amount: >>>>>', amount)
              console.log('_balance: >>>>>', _balance)
              const _avaiable = amountFormat(Number(_balance) - Number(amount));
              // if ()
              setAvaible(String(_avaiable))
            }else if (res.length) {
               const _avaiable = amountFormat(Number(_balance) - res[0].amount);
               // if ()
               setAvaible(String(_avaiable))
             }
          }
          if(search.trader) {
            const dataAllowance = inter.encodeFunctionData('tokenAllowance', [currentAvatar.avatar, search.trader, search.token]);
            const allowance = await avatarContract.staticCall(contracts.plugin.network[SupportedChainId.GOERLI], dataAllowance)
            console.log('allowance: >>>>>', balance, allowance)
            setAllowanceAmount(String(amountFormat(ethers.utils.formatEther(allowance))))
          }

        }
      } catch (e) {
        console.error('e: >>>>>', e)
      }
    })()
  },[account, library, search, active, currentAvatar.avatar, avatarContract, tokens, events, refresh])


  return (
    <div>
      <Box width={500} marginX='auto'>
        <Flex justifyContent="space-between"><h2 style={{marginBottom: 20, fontSize: 20}}>Set Trader</h2> <Text>avaible {avaible} ST1</Text></Flex>
        <Flex flexDirection="column">
          <Text>Trader </Text>
          <Input disabled={setup === 2} type="text" placeholder="Please enter trader address" onChange={(event) => {
            setTrader(event.target.value)
          }} value={trader}/>
        </Flex>
        <Flex flexDirection="column">
          <Text>Allocation</Text>
          <Flex>
            <Select onChange={(option) => {
              setToken(option.value)
            }} options={tokens}/>
            <Input disabled={setup === 1} style={{marginLeft: 10}} type='number' onChange={(event) => {
              setAllowanceAmount(event.target.value)
            }} value={allowanceAmount}/>

          </Flex>
          <Flex justifyContent="flex-end">
            <Button scale="xs" variant="text" onClick={()=>{
              setAllowanceAmount(avaible)
            }}>MAX</Button></Flex>
        </Flex>
        <Flex flexDirection="column" marginY={20}>
          <Flex flexDirection="column">
            <Text>setup 1.</Text>
            <Button isLoading={trader === '' || setup === 2 || actionLock} onClick={async () => {
              await onApprove()
            }}>Approve Trader</Button>
          </Flex>
          <Flex flexDirection="column">
            <Text>setup 2.</Text>
            <Button isLoading={(setup === 1 && Number(allowanceAmount) <= 0)|| actionLock || +allowanceAmount > +avaible} onClick={async () => {
              if(setup === 1) return;
              // await onTokenApprove()
              await onTransfer()

            }}>Allowance</Button>
          </Flex>
        </Flex>
      </Box>
    </div>
  );
}

export default SetTrader;
