import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import useWeb3 from './useWeb3'

/**
 * @desc Get the balance of the current network token
 * */
export default function useCurrentBalance() {
  const [balance, setBalance] = useState(0)
  const { account, chainId } = useWeb3React()
  const web3 = useWeb3()
  useEffect(() => {
    if (account && web3) {
      // @ts-ignore
      web3.eth
        .getBalance(account)
        .then((res: any) => {
          setBalance(Number(Number(res.toString()) / 10 ** 18))
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }, [web3, account, chainId])

  return balance
}
