import React, {useEffect, useState} from 'react';
import {useWeb3React} from "@web3-react/core";
import {ethers} from "ethers";
import {formatEther, parseEther} from "ethers/lib/utils";
import {Box, Button, Flex, Input, Text} from "../../components/uikit";
import useAvatar from "../../contexts/useAvatar";
import useToast from "../../hooks/useToast";
import Select from "../../components/Select/Select";
import contracts from "../../config/constants/contracts";
import {Wallet} from "../../components/uikit/widgets/Menu/icons";

function amountFormat(amount) {
  return Math.floor((+amount || 0) * 10000) / 10000;
}
const tokens = [
  {
    label: 'ST1',
    value: '0x47735caEfa2CCbC2f1C6a37337983dd0442F9755'
  },
  {
    label: 'ST2',
    value: '0xd42D24648b4f4894a8b546fef96c37EBae8cAadB'
  },
]
function Deposit(props) {
  const {account,library } = useWeb3React()
  const {storageFactory, userAvatar, setRefresh, refresh} = useAvatar()
  const {toastSuccess, toastError} = useToast()
  const [actionLock, setActionLock] = useState(false)
  const [currentToken, setCurrentToken] = useState({
    address: tokens[0].value,
    amount: '',
    ST1: '',
    ST2: '',
    label: 'ST1'
  })

  useEffect(()=>{
    (async ()=>{
      if (account) {
        // eslint-disable-next-line no-restricted-syntax
        for (const token of tokens) {
          const erc20 = new ethers.Contract(token.value, contracts.erc20.abi, library.getSigner(account));
          // eslint-disable-next-line no-await-in-loop
          const balance = await erc20.balanceOf(account)
          setCurrentToken(prevState => ({...prevState, [token.label]: formatEther(balance)}))
        }
      }
    })()
  }, [account, library, refresh])
  const onCreate=async ()=>{
    try {
      setActionLock(true)
      console.log('storageFactory: >>>>>', storageFactory)
      const tx = await storageFactory.createStorage(0)
      await tx.wait();
      toastSuccess('Success')
      setActionLock(false)
      setRefresh(prevState=> !prevState)
    }catch (e) {
      toastError('Error', <Flex>
        <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
      </Flex>)
      setActionLock(false)
    }
  }

  const onDeposit=async ()=>{
    try {
      setActionLock(true)
      const erc20 = new ethers.Contract(currentToken.address, contracts.erc20.abi, library.getSigner(account));
      console.log('currentToken.amount: >>>>>', currentToken.amount)
      const tx = await erc20.transfer(userAvatar, parseEther(String(currentToken.amount)))
      await tx.wait();
      setActionLock(false)

      toastSuccess('Success')
      setRefresh(prevState=> !prevState)
    }catch (e){
      toastError('Error', <Flex>
        <Text style={{wordBreak: 'break-word'}}>{e.message}</Text>
      </Flex>)
      setActionLock(false)
    }
  }
  return (
    <div>

      <Box width={500} marginX='auto'>
        <Box marginY={10}><Text fontSize="20px" >Deposit</Text></Box>
        <Flex flexDirection="column">
          <Flex justifyContent="space-between" alignItems="center" paddingX="5px"><Text style={{marginRight: 10}}>
            <Wallet /></Text>
            <Text> {currentToken[currentToken.label]} {currentToken.label}</Text></Flex>
          <Flex>
            <Select onChange={(opt)=>{
              setCurrentToken(prevState => ({...prevState, address: opt.value, label: opt.label}))
            }} options={tokens} />
            <Input style={{marginLeft: 10}} onChange={(event)=>{setCurrentToken(prevState => ({...prevState, amount: event.target.value}))}} value={currentToken.amount}/>
          </Flex>

          <Flex justifyContent="flex-end">
            <Button scale="xs" variant="text" onClick={()=>{
              setCurrentToken(prevState => ({...prevState, amount: prevState[prevState.label]}))
            }}>MAX</Button></Flex>
        </Flex>
        <Button disabled={Number(userAvatar)>0 || actionLock} marginY={10} onClick={()=>{
          onCreate()
        }}>Create Avatar</Button>
        <Button disabled={Number(userAvatar)<=0 || actionLock || +currentToken.amount > +currentToken[currentToken.label]} marginX={10} onClick={()=>{onDeposit()}}>Deposit</Button>
      </Box>
    </div>
  );
}

export default Deposit;
