import React, {Dispatch, SetStateAction, useCallback, useEffect, useMemo, useState} from 'react'
import {ethers} from "ethers";
import {useWeb3React} from "@web3-react/core";
import qs from 'qs'
import {getAddress} from "ethers/lib/utils";
import contracts from "../config/constants/contracts";
import {SupportedChainId} from "../config/constants/chains";

interface Avatar {
  avatar: string
}

const abi = [
  {
    "anonymous": false,
    "inputs": [
      {
        "indexed": false,
        "internalType": "address",
        "name": "user",
        "type": "address"
      },
      {
        "indexed": false,
        "internalType": "uint256",
        "name": "index",
        "type": "uint256"
      },
      {
        "indexed": false,
        "internalType": "address",
        "name": "storageAddr",
        "type": "address"
      }
    ],
    "name": "StorageCreated",
    "type": "event"
  },
  {
    "anonymous": false,
    "inputs": [
      {
        "indexed": false,
        "internalType": "address",
        "name": "trader",
        "type": "address"
      },
      {
        "indexed": false,
        "internalType": "address",
        "name": "token",
        "type": "address"
      },
      {
        "indexed": false,
        "internalType": "uint256",
        "name": "amount",
        "type": "uint256"
      }
    ],
    "name": "UniswapV2Plugin_ApproveTokenSpent",
    "type": "event"
  },
]
const contractInterface = new ethers.utils.Interface(abi);
const AvatarContext = React.createContext<{
  avatarList: Avatar[],
  currentAvatar: Avatar,
  avatarContract: ethers.Contract,
  storageFactory: ethers.Contract,
  userAvatar: string,
  changeAvatar: (address: string) => void,
  setRefresh: any,
  refresh: boolean
}>({
  // eslint-disable-next-line @typescript-eslint/no-empty-function
  avatarContract: undefined, storageFactory: undefined, avatarList: [], changeAvatar(address: string): void {
  }, currentAvatar: undefined, userAvatar: '', setRefresh: undefined, refresh: false
})

// This context maintain 2 counters that can be used as a dependencies on other hooks to force a periodic refresh
const AvatarContextProvider = ({children}) => {
  const {account, library, active} = useWeb3React()
  const [refresh,setRefresh] = useState(false)


  const storageFactory = useMemo(() => new ethers.Contract(
    contracts.storageFactory.network[SupportedChainId.GOERLI],
    new ethers.utils.Interface(contracts.storageFactory.abi),
    library ? library.getSigner(account) : null
  ), [account, library])



  const [userAvatar, setUserAvatar] = useState<string>('')
  const [avatarList,setAvatarList] = useState<Avatar[]>([])

  const [currentAvatar, setCurrentAvatar] = useState({avatar: ''});
  const avatarContract = useMemo(() =>
    currentAvatar.avatar ? new ethers.Contract(currentAvatar.avatar, new ethers.utils.Interface(contracts.user.avatarAbi), library ? library.getSigner(account) : null) : null
    , [account, currentAvatar.avatar, library])

  const changeAvatar = (data: string) => {
    setCurrentAvatar({avatar: data})
  }


  const getAvatar = useCallback(async () => {
    if (account) {
      const avatar = await storageFactory.storages(account, 0);
      setUserAvatar(getAddress(avatar));
    }
  }, [account, storageFactory])
  useEffect(() => {
    if (account) {
      if (window.location.hash.includes('?')) {
        const search = qs.parse(window.location.hash.split('?')[1])
        if (search.avatar) {
          changeAvatar(search.avatar as unknown as string)
        } else {
          changeAvatar('')
        }
      }
    }
  }, [account, refresh])
  useEffect(() => {
    (async () => {
      const avatars: Avatar[] = [];
      // return contracts.user.users.map(user=> ({
      //   avatar: contracts.user[user][SupportedChainId.GOERLI]
      // }))

      if (active) {
        try {
          const avatarLogs = await storageFactory.queryFilter({
            address: contracts.storageFactory.network[SupportedChainId.GOERLI],
            topics: [contractInterface.getEventTopic(contractInterface.getEvent('StorageCreated'))]
          })
          if (avatarLogs.length) {
            // eslint-disable-next-line no-restricted-syntax
            for (const store of avatarLogs) {
              const info = store.args;
              // if (ethers.utils.getAddress(account) === ethers.utils.getAddress(info.user)) {
              //   changeAvatar(ethers.utils.getAddress(info.storageAddr))
              // } else {
              //   changeAvatar('')
              // }
              avatars.push({avatar: ethers.utils.getAddress(info.storageAddr)})
            }
          }

          setAvatarList(avatars)
        }catch (e) {
          console.error(e)
        }
      }
    })()
  }, [account, active, storageFactory, refresh])
  useEffect(() => {
    getAvatar()
  }, [getAvatar, refresh])
  return <AvatarContext.Provider value={{
    avatarList,
    currentAvatar,
    avatarContract,
    changeAvatar,
    storageFactory,
    userAvatar,
    setRefresh,refresh
  }}>{children}</AvatarContext.Provider>
}

export {AvatarContext, AvatarContextProvider}
