import React, { lazy } from 'react'
import { Router, Route, Switch, HashRouter } from 'react-router-dom'
import { ResetCSS } from 'components/uikit'
import BigNumber from 'bignumber.js'
import Compts from 'views/Compts'
import AvatarList from 'views/Avatar/AvatarList'
import SetTrader from 'views/Avatar/SetTrader'
import useEagerConnect from 'hooks/useEagerConnect'
import GlobalStyle from './style/Global'
import Menu from './components/Menu'
import SuspenseWithChunkError from './components/SuspenseWithChunkError'
import ToastListener from './components/ToastListener'
import PageLoader from './components/PageLoader'
import Trading from "./views/Avatar/Swap";
import Deposit from "./views/Avatar/Deposit";


const NotFound = lazy(() => import('./views/NotFound'))

// This config is required for number formatting
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
})

const App: React.FC = () => {
  useEagerConnect()

  return (
    <HashRouter>
      <ResetCSS />
      <GlobalStyle />
      <Menu>
        <SuspenseWithChunkError fallback={<PageLoader />}>
          <Switch>
            <Route path="/" exact component={AvatarList} />
            <Route path="/trade" exact component={SetTrader} />
            <Route path="/trading" exact component={Trading} />
            <Route path="/deposit" exact component={Deposit} />

            <Route path="/comp" exact component={Compts} />
            {/* 404 */}
            <Route component={NotFound} />
          </Switch>
        </SuspenseWithChunkError>
      </Menu>
      <ToastListener />
    </HashRouter>
  )
}

export default React.memo(App)
