  const [events,setLogs] = useState([]);

  useEffect(() => {
    (async ()=>{
      const filter = {
        address: contracts.plugin.network[SupportedChainId.GOERLI],
        topics: [eventTopic]
      }
      console.log('avatarList: >>>>>', avatarList)
      if (avatarList.length) {
        console.log('avatarList: >>>>>', avatarList)
        // eslint-disable-next-line no-restricted-syntax
        for (const {avatar} of avatarList) {
          const avatarContract = new ethers.Contract(avatar, contractInterface, provider)
          avatarContract.queryFilter(filter).then(async (result)=>{
            const _logs = [];
            // eslint-disable-next-line no-restricted-syntax
            for (const log of result) {
              const _log = {
                trader: '',
                token: '',
                symbol: '',
                avatar,
                blockNumber: 0,
                amount: undefined
              }
              const decodedLog = log.args as unknown as ApproveTokenSpent;
              const erc20Token = new ethers.Contract(decodedLog.token, contracts.erc20.abi, provider)
              // eslint-disable-next-line no-await-in-loop
              const tokenSymbol = await erc20Token.symbol()
              _log.trader = ethers.utils.getAddress(decodedLog.trader) ;
              _log.token = decodedLog.token;
              _log.amount = ethers.utils.formatEther(decodedLog.amount);
              _log.blockNumber = log.blockNumber;
              _log.symbol = tokenSymbol;
              _logs.push(_log)
            }
            // logs.push()
            setLogs(prevState => {
              const nextState = [
                ...prevState,
                ...filterData(_logs)
              ]
              return filterData(nextState)
            })
          })
        }
      }
    })()
  }, [eventTopic, avatarList, provider, account, storageFactory, changeAvatar, refresh]);


  return events
