import { BigNumber } from '@ethersproject/bignumber'
import { hexStripZeros } from '@ethersproject/bytes'
import { CHAIN_INFO } from '../config/constants/chainInfo'
import { SupportedChainId } from '../config/constants/chains'
import { INFURA_NETWORK_URLS } from '../config/constants/infura'

interface SwitchNetworkArguments {
  connector: any
  chainId: SupportedChainId
}

function getRpcUrls(chainId: SupportedChainId): [string] {
  switch (chainId) {
    case SupportedChainId.MAINNET:
    case SupportedChainId.RINKEBY:
    case SupportedChainId.ROPSTEN:
    case SupportedChainId.KOVAN:
    case SupportedChainId.GOERLI:
      return [INFURA_NETWORK_URLS[chainId]]
    case SupportedChainId.OPTIMISM:
      return ['https://mainnet.optimism.io']
    case SupportedChainId.OPTIMISTIC_KOVAN:
      return ['https://kovan.optimism.io']
    case SupportedChainId.ARBITRUM_ONE:
      return ['https://arb1.arbitrum.io/rpc']
    case SupportedChainId.ARBITRUM_RINKEBY:
      return ['https://rinkeby.arbitrum.io/rpc']
    case SupportedChainId.POLYGON:
      return ['https://polygon-rpc.com/']
    case SupportedChainId.POLYGON_MUMBAI:
      return ['https://rpc-endpoints.superfluid.dev/mumbai']
    default:
  }
  // Our API-keyed URLs will fail security checks when used with external wallets.
  throw new Error('RPC URLs must use public endpoints')
}

// provider.request returns Promise<any>, but wallet_switchEthereumChain must return null or throw
// see https://github.com/rekmarks/EIPs/blob/3326-create/EIPS/eip-3326.md for more info on wallet_switchEthereumChain
export default async function switchToNetwork({ connector, chainId }: SwitchNetworkArguments): Promise<null | void> {
  connector.getProvider().then(async res=>{
    const formattedChainId = hexStripZeros(BigNumber.from(chainId).toHexString())
    try {
      await res.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: formattedChainId }],
      })
    } catch (error) {
      // 4902 is the error code for attempting to switch to an unrecognized chainId
      // @ts-ignore
      if (error.code === 4902) {
        const info = CHAIN_INFO[chainId]

        await res.request({
          method: 'wallet_addEthereumChain',
          params: [
            {
              chainId: formattedChainId,
              chainName: info.label,
              rpcUrls: getRpcUrls(chainId),
              nativeCurrency: info.nativeCurrency,
              blockExplorerUrls: [info.explorer],
            },
          ],
        })
        // metamask (only known implementer) automatically switches after a network is added
        // the second call is done here because that behavior is not a part of the spec and cannot be relied upon in the future
        // metamask's behavior when switching to the current network is just to return null (a no-op)
        try {
          await res.request({
            method: 'wallet_switchEthereumChain',
            params: [{ chainId: formattedChainId }],
          })
          // eslint-disable-next-line @typescript-eslint/no-shadow
        } catch (error) {
          // eslint-disable-next-line no-console
          console.debug('Added network but could not switch chains', error)
        }
      } else {
        throw error
      }
    }
  }).catch(e=>{
    // eslint-disable-next-line no-console
    console.log(e)
  })

}
