import React from 'react'
import styled from "styled-components";
import Button from '../../../components/Button/Button'
import { Login, useWalletModal }  from '../../WalletModal'
import {Wallet} from "../icons";
import useCurrentBalance from "../../../../../hooks/useCurrentBalance";

interface Props {
  account?: string
  login: Login
  logout: () => void
  balance?: number
}
const WalletButon = styled(Button)`{
  color: #2AA1FF;
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid rgba(207, 216, 226, 0.7);
  box-sizing: border-box;
  backdrop-filter: blur(10px);
  font-weight: 500;
  font-size: 12px;
  height: 28px;
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 20px;
  svg {
    width: 17px;
    height: 13px;
    margin-right: 9px;
  }
  span{
    margin-left: 5px;
  }
}`
const UserBlock: React.FC<Props> = ({ account, login, logout }) => {
  const { onPresentConnectModal, onPresentAccountModal } = useWalletModal(login, logout, account)
  const balance = useCurrentBalance();
  const balanceEllipsis = balance ? `${balance.toString().substring(0, 10)}` : '0.00';

  return (
    <div>
      {account ? (
        <WalletButon
          scale="sm"
          variant="tertiary"
          onClick={() => {
            onPresentAccountModal()
          }}

        >
          <Wallet /> {balanceEllipsis} <span>ETH</span>
        </WalletButon>
      ) : (
        <Button
          scale="sm"
          onClick={() => {
            onPresentConnectModal()
          }}
        >
          Connect
        </Button>
      )}
    </div>
  )
}

export default React.memo(UserBlock, (prevProps, nextProps) => prevProps.account === nextProps.account)
